				 @imgDelete="mediaDelete">
				</jade-image-upload>		
			</view>			
		</view>
		<view class="bttn">
			<button type="primary" @click="suub" style="background-color: #2879fc; font-size: 28rpx;">提交</button>
		</view>
		<view>

		</view>	
	</view>
</template>

<script>
	export default {
