				// 	method: 'GET',
				// 	success: (res) => {
					//	let token = res.data.data; //拿到上传七牛所必须的token
					//	urlList.forEach((item) => {
							this.uploadTask = uni.uploadFile({
								url: this.apiUrl+'/index.php/index/upyyzz', //七牛上传接口，仅为示例
								filePath:urlList[0].src,
								name: 'file',
								// formData: {
								// 	'token': token
								// },
								header: {
									'Content-Type': 'multipart/form-data' 
								//	accept: 'application/json'
										},
								success: (res) => {
									console.log("上传")
									console.log(res)
									this.imgurl  = res.data
									console.log(this.imgurl)
								}
							})
		
								//	let data = JSON.parse(res.data) //七牛返回的数据
									// if (!data.data.url) {
									// 	item.status = 'error'
									// 	item.progress = '上传失败'
									// } else {
									// 	item.status = 'success'
									// 	item.progress = '上传成功'
									// 	item.src = data.data.url
									// }
								
							
						},
						suub(){
							
									uni.request({
										method:"POST",
										url:this.apiUrl+"index.php/index/yjfk",
										data:JSON.stringify({
											"user_name":uni.getStorageSync("openid"),
											"yhm":this.userInfo.yhm,
										"title":this.title,
											"jy":this.jyi,
											"lxfs":this.phone,
											'tplj': this.imgurl
